"use server";

import { prisma } from "@/lib/prisma";
import { User } from "@prisma/client";

export type ParticipationStatus = {
  userId: string;
  name: string;
  handle: string | null;
  participated: boolean;
  rank?: number;
  score?: number;
  details?: any;
  error?: string;
};

// --- Codeforces ---

async function checkCodeforcesParticipation(contestId: string, users: User[]): Promise<ParticipationStatus[]> {
  const usersWithHandle = users.filter(u => u.codeforcesHandle);
  const handleMap = new Map<string, User>();
  usersWithHandle.forEach(u => handleMap.set(u.codeforcesHandle!.toLowerCase(), u));

  if (usersWithHandle.length === 0) {
    return users.map(u => ({
      userId: u.id,
      name: u.name,
      handle: u.codeforcesHandle || null,
      participated: false,
      error: "No Codeforces handle linked"
    }));
  }

  // Codeforces allows multiple handles in one request (up to some limit, usually 10000 characters url length)
  // chunk handles if necessary
  const chunks = [];
  const chunkSize = 100; // conservative batch size
  for (let i = 0; i < usersWithHandle.length; i += chunkSize) {
    chunks.push(usersWithHandle.slice(i, i + chunkSize));
  }

  const results: ParticipationStatus[] = [];
  // Initialize all as not participated
  users.forEach(u => {
      if (!u.codeforcesHandle) {
          results.push({
              userId: u.id,
              name: u.name,
              handle: null,
              participated: false,
              error: "No handle"
          });
      }
  });

  for (const chunk of chunks) {
    const handles = chunk.map(u => u.codeforcesHandle).join(";");
    try {
      const res = await fetch(`https://codeforces.com/api/contest.standings?contestId=${contestId}&handles=${handles}&showUnofficial=true`);
      const data = await res.json();

      if (data.status !== "OK") {
        chunk.forEach(u => results.push({
          userId: u.id,
          name: u.name,
          handle: u.codeforcesHandle,
          participated: false,
          error: data.comment || "Codeforces API Error"
        }));
        continue;
      }

      // Process rows
      const rows = data.result.rows;
      // Mark those present as participated
      const foundHandles = new Set<string>();

      rows.forEach((row: any) => {
        // row.party.members contains the handles
        row.party.members.forEach((member: any) => {
           const handle = member.handle.toLowerCase();
           const user = handleMap.get(handle);
           if (user) {
             foundHandles.add(handle);
             results.push({
               userId: user.id,
               name: user.name,
               handle: user.codeforcesHandle,
               participated: true,
               rank: row.rank,
               score: row.points,
               details: {
                 penalty: row.penalty,
                 successfulHackCount: row.successfulHackCount
               }
             });
           }
        });
      });

      // Those in the chunk but not found in rows => did not participate
      chunk.forEach(u => {
        if (u.codeforcesHandle && !foundHandles.has(u.codeforcesHandle.toLowerCase())) {
          results.push({
            userId: u.id,
            name: u.name,
            handle: u.codeforcesHandle,
            participated: false
          });
        }
      });

    } catch (e: any) {
      chunk.forEach(u => results.push({
        userId: u.id,
        name: u.name,
        handle: u.codeforcesHandle,
        participated: false,
        error: e.message
      }));
    }
  }

  return results;
}

// --- LeetCode ---

async function checkLeetCodeParticipation(contestSlug: string, users: User[]): Promise<ParticipationStatus[]> {
  // LeetCode doesn't have a batch check. We must check each user.
  // We'll run in parallel with concurrency limit.

  const results: ParticipationStatus[] = [];

  const checkUser = async (user: User) => {
    if (!user.leetCodeHandle) {
      return {
        userId: user.id,
        name: user.name,
        handle: null,
        participated: false,
        error: "No handle"
      };
    }

    try {
      // Query user's contest history
      const query = `
        query userContestRankingInfo($username: String!) {
          userContestRankingHistory(username: $username) {
            attended
            contest {
              titleSlug
              startTime
            }
            ranking
            finishTimeInSeconds
          }
        }
      `;

      const res = await fetch("https://leetcode.com/graphql", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Referer": `https://leetcode.com/u/${user.leetCodeHandle}/`
        },
        body: JSON.stringify({
          query,
          variables: { username: user.leetCodeHandle }
        }),
        cache: "no-store"
      });

      const data = await res.json();
      if (data.errors) {
         return {
            userId: user.id,
            name: user.name,
            handle: user.leetCodeHandle,
            participated: false,
            error: data.errors[0]?.message
         };
      }

      const history = data.data.userContestRankingHistory || [];
      // Find the contest
      const participation = history.find((p: any) => p.contest?.titleSlug === contestSlug);

      if (participation && participation.attended) {
        return {
          userId: user.id,
          name: user.name,
          handle: user.leetCodeHandle,
          participated: true,
          rank: participation.ranking,
          details: {
            finishTime: participation.finishTimeInSeconds
          }
        };
      } else {
        return {
          userId: user.id,
          name: user.name,
          handle: user.leetCodeHandle,
          participated: false
        };
      }

    } catch (e: any) {
      return {
        userId: user.id,
        name: user.name,
        handle: user.leetCodeHandle,
        participated: false,
        error: e.message
      };
    }
  };

  // Process in chunks to avoid rate limits
  const chunkSize = 5;
  for (let i = 0; i < users.length; i += chunkSize) {
    const chunk = users.slice(i, i + chunkSize);
    const chunkResults = await Promise.all(chunk.map(checkUser));
    results.push(...chunkResults);
  }

  return results;
}

// --- CodeChef ---

async function checkCodeChefParticipation(contestCode: string, users: User[]): Promise<ParticipationStatus[]> {
  const results: ParticipationStatus[] = [];

  const checkUser = async (user: User) => {
    if (!user.codeChefHandle) {
      return {
        userId: user.id,
        name: user.name,
        handle: null,
        participated: false,
        error: "No handle"
      };
    }

    try {
      // Use the ranking search API
      const res = await fetch(`https://www.codechef.com/api/rankings/${contestCode}?itemsPerPage=1&search=${user.codeChefHandle}`, {
          cache: "no-store"
      });

      if (!res.ok) { // Contest might be invalid or other error
         return {
            userId: user.id,
            name: user.name,
            handle: user.codeChefHandle,
            participated: false,
            error: `API Status: ${res.status}`
         };
      }

      const data = await res.json();

      // data.list contains the rows
      // We searched for the specific handle. If it's there and exact match...
      const entry = data.list?.find((item: any) => item.user_handle?.toLowerCase() === user.codeChefHandle?.toLowerCase());

      if (entry) {
        return {
          userId: user.id,
          name: user.name,
          handle: user.codeChefHandle,
          participated: true,
          rank: entry.rank,
          score: parseFloat(entry.score),
          details: {
             penalty: entry.penalty
          }
        };
      } else {
        return {
          userId: user.id,
          name: user.name,
          handle: user.codeChefHandle,
          participated: false
        };
      }

    } catch (e: any) {
      return {
        userId: user.id,
        name: user.name,
        handle: user.codeChefHandle,
        participated: false,
        error: e.message
      };
    }
  };

   // Process in chunks
  const chunkSize = 5;
  for (let i = 0; i < users.length; i += chunkSize) {
    const chunk = users.slice(i, i + chunkSize);
    const chunkResults = await Promise.all(chunk.map(checkUser));
    results.push(...chunkResults);
  }

  return results;
}


// --- Main Action ---

export async function checkExternalParticipation(
  platform: "CodeForces" | "LeetCode" | "CodeChef",
  contestIdentifier: string, // ID for CF, Slug for LC, Code for CC
  userFilters?: {
      institutionId?: string;
      classroomId?: string;
  }
) {

  // 1. Fetch Users
  const where: any = {
      role: "STUDENT" // Mostly we care about students
  };

  if (userFilters?.institutionId) {
      where.institutionId = userFilters.institutionId;
  }

  // Note: Classroom logic might need recursive fetch if not directly on user,
  // but User model has `enrolledClassrooms`.
  if (userFilters?.classroomId) {
      where.enrolledClassrooms = {
          some: {
              id: userFilters.classroomId
          }
      };
  }

  const users = await prisma.user.findMany({
    where,
    select: {
      id: true,
      name: true,
      email: true,
      codeforcesHandle: true,
      leetCodeHandle: true,
      codeChefHandle: true
    }
  });

  if (users.length === 0) {
      return { success: true, results: [] };
  }

  let results: ParticipationStatus[] = [];

  try {
      if (platform === "CodeForces") {
          results = await checkCodeforcesParticipation(contestIdentifier, users as User[]);
      } else if (platform === "LeetCode") {
          results = await checkLeetCodeParticipation(contestIdentifier, users as User[]);
      } else if (platform === "CodeChef") {
          results = await checkCodeChefParticipation(contestIdentifier, users as User[]);
      }

      return { success: true, results };
  } catch (error: any) {
      console.error("Check participation error:", error);
      return { success: false, error: error.message };
  }
}
