"use server";

import { prisma } from "@/lib/prisma";
import { checkExternalParticipation } from "./contest-checker.action";

// Re-export external checker for ease of use in client components
export { checkExternalParticipation };

export type InternalContestParticipation = {
    userId: string;
    name: string;
    email: string;
    participated: boolean;
    score: number;
    rank?: number;
    problemsSolved: number;
    finishedAt?: Date | null;
    isFinished: boolean;
};

export async function checkInternalContestParticipation(
    contestId: string,
    classroomId: string
): Promise<{ success: boolean; results?: InternalContestParticipation[]; error?: string }> {
    try {
        // 1. Verify contest exists
        const contest = await prisma.contest.findUnique({
            where: { id: contestId },
            select: { id: true, title: true }
        });

        if (!contest) {
            return { success: false, error: "Contest not found" };
        }

        // 2. Get all students in the classroom
        const classroom = await prisma.classroom.findUnique({
            where: { id: classroomId },
            include: {
                students: {
                    select: { id: true, name: true, email: true },
                    orderBy: { name: 'asc' }
                }
            }
        });

        if (!classroom) {
            return { success: false, error: "Classroom not found" };
        }

        // 3. Get participation records for this contest and these students
        const participations = await prisma.contestParticipation.findMany({
            where: {
                contestId: contestId,
                userId: {
                    in: classroom.students.map(s => s.id)
                }
            },
            include: {
                user: {
                    select: {
                         submissions: {
                            where: { contestId: contestId, status: "ACCEPTED" },
                            select: { problemId: true, problem: { select: { score: true } } }
                         }
                    }
                }
            }
        });

        // 4. Map results
        const participationMap = new Map();
        participations.forEach(p => {
            // Calculate score based on unique problems solved
            const uniqueSolved = new Set();
            let totalScore = 0;

            p.user.submissions.forEach(sub => {
                if (!uniqueSolved.has(sub.problemId)) {
                    uniqueSolved.add(sub.problemId);
                    totalScore += sub.problem.score;
                }
            });

            participationMap.set(p.userId, {
                participated: true,
                finishedAt: p.finishedAt,
                isFinished: p.isFinished,
                score: totalScore,
                problemsSolved: uniqueSolved.size
            });
        });

        const results: InternalContestParticipation[] = classroom.students.map(student => {
            const data = participationMap.get(student.id);
            return {
                userId: student.id,
                name: student.name || "Unknown",
                email: student.email,
                participated: !!data,
                score: data?.score || 0,
                problemsSolved: data?.problemsSolved || 0,
                finishedAt: data?.finishedAt || null,
                isFinished: data?.isFinished || false
            };
        });

        // Add ranks for those who participated
        // Rank by Score (desc), then Time (asc) - not implementing complex time penalty logic here for simplicity,
        // just score.
        results
            .filter(r => r.participated)
            .sort((a, b) => b.score - a.score)
            .forEach((r, idx) => {
                r.rank = idx + 1;
            });

        return { success: true, results };

    } catch (error: any) {
        console.error("Internal contest check failed:", error);
        return { success: false, error: "Failed to fetch participation data" };
    }
}
