"use client";

import { useState } from "react";
import { checkInternalContestParticipation, InternalContestParticipation } from "@/actions/classroom-contest.action";
import { checkExternalParticipation, ParticipationStatus } from "@/actions/contest-checker.action";
import { Search, Loader2, Trophy, Globe, CheckCircle2, XCircle, ExternalLink, Calendar } from "lucide-react";

interface ContestVerificationTabProps {
    classroomId: string;
}

export function ContestVerificationTab({ classroomId }: ContestVerificationTabProps) {
    const [mode, setMode] = useState<"internal" | "external">("internal");

    // Internal State
    const [internalContestId, setInternalContestId] = useState("");
    const [internalResults, setInternalResults] = useState<InternalContestParticipation[] | null>(null);

    // External State
    const [platform, setPlatform] = useState<"CodeForces" | "LeetCode" | "CodeChef">("CodeForces");
    const [externalContestId, setExternalContestId] = useState("");
    const [externalResults, setExternalResults] = useState<ParticipationStatus[] | null>(null);

    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState("");

    const handleInternalCheck = async () => {
        if (!internalContestId) {
            setError("Please enter a Contest ID");
            return;
        }
        setIsLoading(true);
        setError("");
        setInternalResults(null);

        const res = await checkInternalContestParticipation(internalContestId, classroomId);

        if (res.success) {
            setInternalResults(res.results!);
        } else {
            setError(res.error || "Failed to fetch data");
        }
        setIsLoading(false);
    };

    const handleExternalCheck = async () => {
        if (!externalContestId) {
            setError("Please enter a Contest ID/Slug");
            return;
        }
        setIsLoading(true);
        setError("");
        setExternalResults(null);

        const res = await checkExternalParticipation(platform, externalContestId, { classroomId });

        if (res.success) {
            setExternalResults(res.results!);
        } else {
            setError(res.error || "Failed to fetch data");
        }
        setIsLoading(false);
    };

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                     <h2 className="text-xl font-bold text-gray-900 dark:text-white">Contest Participation Verification</h2>
                     <p className="text-sm text-gray-500 dark:text-gray-400">Verify if your students attended internal or external contests.</p>
                </div>

                <div className="flex bg-gray-100 dark:bg-[#1a1a1a] p-1 rounded-lg">
                    <button
                        onClick={() => setMode("internal")}
                        className={`px-4 py-1.5 rounded-md text-sm font-medium transition-all ${
                            mode === "internal"
                            ? "bg-white dark:bg-[#262626] text-orange-600 dark:text-orange-500 shadow-sm"
                            : "text-gray-500 hover:text-gray-900 dark:hover:text-gray-200"
                        }`}
                    >
                        Internal
                    </button>
                     <button
                        onClick={() => setMode("external")}
                        className={`px-4 py-1.5 rounded-md text-sm font-medium transition-all ${
                            mode === "external"
                            ? "bg-white dark:bg-[#262626] text-orange-600 dark:text-orange-500 shadow-sm"
                            : "text-gray-500 hover:text-gray-900 dark:hover:text-gray-200"
                        }`}
                    >
                        External
                    </button>
                </div>
            </div>

            <div className="bg-white dark:bg-[#141414] border border-gray-200 dark:border-[#262626] rounded-xl p-6 shadow-sm">

                {/* Controls */}
                <div className="flex flex-col md:flex-row gap-4 items-end">
                    {mode === "internal" ? (
                        <div className="flex-1 space-y-2 w-full">
                            <label className="text-sm font-medium text-gray-700 dark:text-gray-300">Algo-Fox Contest ID</label>
                            <div className="relative">
                                <Trophy className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                                <input
                                    type="text"
                                    value={internalContestId}
                                    onChange={(e) => setInternalContestId(e.target.value)}
                                    placeholder="Enter internal contest ID..."
                                    className="w-full h-10 pl-10 pr-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
                                />
                            </div>
                        </div>
                    ) : (
                        <>
                             <div className="space-y-2 w-full md:w-48">
                                <label className="text-sm font-medium text-gray-700 dark:text-gray-300">Platform</label>
                                <select
                                    value={platform}
                                    onChange={(e) => setPlatform(e.target.value as any)}
                                    className="w-full h-10 px-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
                                >
                                    <option value="CodeForces">CodeForces</option>
                                    <option value="LeetCode">LeetCode</option>
                                    <option value="CodeChef">CodeChef</option>
                                </select>
                            </div>
                            <div className="flex-1 space-y-2 w-full">
                                <label className="text-sm font-medium text-gray-700 dark:text-gray-300">
                                    {platform === "CodeForces" ? "Contest ID" : platform === "LeetCode" ? "Contest Slug" : "Contest Code"}
                                </label>
                                <div className="relative">
                                    <Globe className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                                    <input
                                        type="text"
                                        value={externalContestId}
                                        onChange={(e) => setExternalContestId(e.target.value)}
                                        placeholder={
                                            platform === "CodeForces" ? "e.g. 1950" :
                                            platform === "LeetCode" ? "e.g. weekly-contest-390" :
                                            "e.g. START128"
                                        }
                                        className="w-full h-10 pl-10 pr-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
                                    />
                                </div>
                            </div>
                        </>
                    )}

                    <button
                        onClick={mode === "internal" ? handleInternalCheck : handleExternalCheck}
                        disabled={isLoading}
                        className="w-full md:w-auto px-6 h-10 bg-orange-600 hover:bg-orange-700 text-white rounded-lg text-sm font-medium transition-colors flex items-center justify-center gap-2 disabled:opacity-50"
                    >
                        {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Search className="w-4 h-4" />}
                        Check
                    </button>
                </div>

                {error && (
                    <div className="mt-4 p-3 bg-red-50 dark:bg-red-900/10 text-red-600 dark:text-red-400 text-sm rounded-lg flex items-center gap-2">
                        <XCircle className="w-4 h-4" />
                        {error}
                    </div>
                )}
            </div>

            {/* Internal Results */}
            {mode === "internal" && internalResults && (
                <div className="bg-white dark:bg-[#141414] border border-gray-200 dark:border-[#262626] rounded-xl overflow-hidden shadow-sm">
                     <div className="px-6 py-4 border-b border-gray-200 dark:border-[#262626] bg-gray-50/50 dark:bg-[#1a1a1a]/50 flex justify-between items-center">
                        <h3 className="font-semibold text-gray-900 dark:text-white">Results ({internalResults.length})</h3>
                        <div className="flex gap-4 text-sm">
                            <span className="text-green-600 dark:text-green-500 font-medium">
                                {internalResults.filter(r => r.participated).length} Present
                            </span>
                            <span className="text-red-600 dark:text-red-500 font-medium">
                                {internalResults.filter(r => !r.participated).length} Absent
                            </span>
                        </div>
                    </div>
                    <div className="overflow-x-auto">
                        <table className="w-full text-sm text-left">
                            <thead className="bg-gray-50 dark:bg-[#1a1a1a] text-gray-500 dark:text-gray-400 font-medium border-b border-gray-200 dark:border-[#262626]">
                                <tr>
                                    <th className="px-6 py-3">Student</th>
                                    <th className="px-6 py-3">Email</th>
                                    <th className="px-6 py-3">Status</th>
                                    <th className="px-6 py-3">Score</th>
                                    <th className="px-6 py-3">Solved</th>
                                    <th className="px-6 py-3">Finished At</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-gray-200 dark:divide-[#262626]">
                                {internalResults.map((r) => (
                                    <tr key={r.userId} className="hover:bg-gray-50 dark:hover:bg-[#1a1a1a]/50 transition-colors">
                                        <td className="px-6 py-3 font-medium text-gray-900 dark:text-white">{r.name}</td>
                                        <td className="px-6 py-3 text-gray-500 dark:text-gray-400">{r.email}</td>
                                        <td className="px-6 py-3">
                                            {r.participated ? (
                                                <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-500">
                                                    Present
                                                </span>
                                            ) : (
                                                <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-500">
                                                    Absent
                                                </span>
                                            )}
                                        </td>
                                        <td className="px-6 py-3 font-mono">{r.score}</td>
                                        <td className="px-6 py-3 font-mono">{r.problemsSolved}</td>
                                        <td className="px-6 py-3 text-gray-500 dark:text-gray-400">
                                            {r.finishedAt ? new Date(r.finishedAt).toLocaleString() : "-"}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            )}

            {/* External Results */}
            {mode === "external" && externalResults && (
                <div className="bg-white dark:bg-[#141414] border border-gray-200 dark:border-[#262626] rounded-xl overflow-hidden shadow-sm">
                    <div className="px-6 py-4 border-b border-gray-200 dark:border-[#262626] bg-gray-50/50 dark:bg-[#1a1a1a]/50 flex justify-between items-center">
                        <h3 className="font-semibold text-gray-900 dark:text-white">Results ({externalResults.length})</h3>
                         <div className="flex gap-4 text-sm">
                            <span className="text-green-600 dark:text-green-500 font-medium">
                                {externalResults.filter(r => r.participated).length} Present
                            </span>
                            <span className="text-red-600 dark:text-red-500 font-medium">
                                {externalResults.filter(r => !r.participated).length} Absent
                            </span>
                        </div>
                    </div>
                    <div className="overflow-x-auto">
                        <table className="w-full text-sm text-left">
                             <thead className="bg-gray-50 dark:bg-[#1a1a1a] text-gray-500 dark:text-gray-400 font-medium border-b border-gray-200 dark:border-[#262626]">
                                <tr>
                                    <th className="px-6 py-3">Student</th>
                                    <th className="px-6 py-3">Handle</th>
                                    <th className="px-6 py-3">Status</th>
                                    <th className="px-6 py-3">Rank</th>
                                    <th className="px-6 py-3">Details</th>
                                </tr>
                            </thead>
                             <tbody className="divide-y divide-gray-200 dark:divide-[#262626]">
                                {externalResults.map((r) => (
                                    <tr key={r.userId} className="hover:bg-gray-50 dark:hover:bg-[#1a1a1a]/50 transition-colors">
                                        <td className="px-6 py-3 font-medium text-gray-900 dark:text-white">{r.name}</td>
                                        <td className="px-6 py-3 text-gray-500 dark:text-gray-400 font-mono">
                                            {r.handle ? (
                                                <a href={
                                                    platform === "CodeForces" ? `https://codeforces.com/profile/${r.handle}` :
                                                    platform === "LeetCode" ? `https://leetcode.com/u/${r.handle}` :
                                                    `https://www.codechef.com/users/${r.handle}`
                                                } target="_blank" rel="noopener noreferrer" className="hover:text-orange-500 flex items-center gap-1">
                                                    {r.handle}
                                                    <ExternalLink className="w-3 h-3" />
                                                </a>
                                            ) : (
                                                <span className="text-gray-400 italic">Not linked</span>
                                            )}
                                        </td>
                                         <td className="px-6 py-3">
                                            {r.error ? (
                                                <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-500">
                                                    Error
                                                </span>
                                            ) : r.participated ? (
                                                <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-500">
                                                    Participated
                                                </span>
                                            ) : (
                                                <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-500">
                                                    Absent
                                                </span>
                                            )}
                                        </td>
                                        <td className="px-6 py-3 text-gray-600 dark:text-gray-400">
                                            {r.rank ? `#${r.rank}` : "-"}
                                        </td>
                                        <td className="px-6 py-3 text-xs text-gray-500 dark:text-gray-400 max-w-xs truncate">
                                             {r.error ? r.error : JSON.stringify(r.details || {})}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            )}
        </div>
    );
}
